.slider {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
}

.slider--mobile {
  display: flex;

  @media (min-width: $tablet) {
    display: none;
  }
}

.slider__item {
  &:not(:last-child) {
    margin-right: 19px;
  }
}

.slider__item--active .slider__button {
  background-color: $white;
  box-shadow: 0 0 0 3px #283645;
}

.slider__button {
  width: 6px;
  height: 6px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background-color: rgba($color: #283645, $alpha: 0.3);
}
